.App {
  text-align: center;
}
.user1, .user2{
 height:100vh;
 }
.battleField{
  background-image: url("https://www.ricklohre.com/wp-content/uploads/2018/10/Aerial-Drone-Photography-1-9.jpg");
  background-position: center;
}
.endMessageWin{
  display: table;
  position: relative;
  margin: 40px auto 0;
  width: 500px;
  background-color: green;
  color: #fff;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  border-radius: 1rem;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: slidein;
          animation-name: slidein;
};

@-webkit-keyframes slidein {
  from {
    margin-left: 100%;
    width: 223%; 
  }

  to {
    margin-left: 0%;
    width: 223%;
  }
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 223%; 
  }

  to {
    margin-left: 0%;
    width: 223%;
  }
}


.endMessageLose{
  display: table;
  position: relative;
  margin: 40px auto 0;
  width: 500px;
  background-color: red;
  color: #fff;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  border-radius: 1rem;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: slidein;
          animation-name: slidein;
}
@keyframes slidein {
  from {
    margin-left: 100%;
    width: 223%; 
  }

  to {
    margin-left: 0%;
    width: 223%;
  }
}

